import Vue from 'vue'
import All from './All.vue'
import VueSocketIO from 'vue-socket.io'

console.log('Origin:', window.location.origin)

Vue.config.productionTip = false
Vue.use(new VueSocketIO({
  debug: true,
  connection: window.location.origin,
  // options: {path: '/'}
}))

let app = new Vue({
  render: h => h(All),
}).$mount('#app')

console.log(app)